li {
  list-style: none;
}

.industry-list li {
  list-style-type: disc;
  margin: 10px;
}

.industry-traction {
  background-color: hsl(0, 0%, 98%);
  margin: auto;
  width: 80%;
}

.industry-traction p {
  width: 80%;
  margin-left: 15px;
}

.title {
  font-size: 12vw;
  font-weight: 800;
  line-height: 12vw;
  margin-left: 10%;
  color: black;
}

.industry-card {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 80%;
  margin: auto;
  margin-top: 20px;
}

.name {
  background-color: red;
  width: 18vh;
  padding: 2vh;
  font-size: 2vw;
}

.percentage {
  font-size: 1.2rem;
  padding-top: 2px;
}

.description {
  font-size: 2.3vw;
  justify-content: center;
}

.industry-image {
  width: 100%;
}

.crypto {
  background-color: #1055a5;
  color: white;
}

.gaming {
  background-color: rgb(88, 148, 238);
}

.fintech {
  background-color: rgb(165, 248, 244);
}

.industry-traction hr {
  width: 110%;
  margin-left: -5%;
  margin-top: 10vh;
  margin-bottom: 7vh;
}

@media only screen and (min-width: 768px) {
  .title {
    font-size: 10vw;
    font-weight: 800;
    line-height: 10vw;
  }

  .illustration {
    display: grid;
    grid-template-columns: 4fr 3fr;
  }

  .industry-percentage {
    display: flex;
    flex-direction: column;
  }

  .industry-card {
    margin: auto;
  }

  .name {
    font-size: 1.2vw;
  }

  .description {
    font-size: 1.2vw;
  }
}
