.new-crypto {
  width: 80%;
  margin: auto;
  background-color: hsl(0, 0%, 98%);
}

.new-crypto h1 {
  color: black;
  width: 80%;
  padding: 20px;
  font-size: 8.2vw;
}

.hr-bar {
  background-color: rgb(169, 265, 250) !important;
  border: none;
  height: 1vh;
  width: 25%;
  margin-left: 30%;
  margin-top: -13%;
  margin-bottom: 10%;
}

.crypto-para {
  width: 80%;
  margin: auto;
  padding: 20px;
}

.crypto-info-container {
  width: 125%;
  margin-left: -12.5%;
  margin-top: 50px;
  background-color: red;
  background-image: linear-gradient(to right, rgb(169, 245, 250), rgb(88, 148, 238));
}

.crypto-container {
  width: 80%;
  margin: auto;
  background-color: hsl(0, 0%, 98%);
}

.crypto-info {
  display: flex;
  margin: 20px;
}

.info-card {
  display: flex;
  flex-direction: row;
  background-color: rgb(169, 245, 250);
  margin: 10px 20px;
  padding: 10px;
}

.info-card h3 {
  margin-right: 3vw;
  font-size: 3vw;
}

.info-card p {
  font-size: 2vw;
}

.light-green {
  background-color: rgb(169, 245, 250);
}

.blue {
  background-color: rgb(88, 148, 238);
}

.crypto-image {
  width: 85%;
  margin-left: 7.5%;
}

@media only screen and (min-width: 500px) {
  .hr-bar {
    height: 1.8vh;
    margin-top: -12%;
  }

  .grid-container {
    display: grid;
    grid-template-columns: auto auto;
  }

  .info-card {
    margin: 5px 10px;
    padding: 10px;
  }

  .info-card h3 {
    font-size: 2vw;
  }

  .info-card p {
    font-size: 1.8vw;
  }
}

@media only screen and (min-width: 768px) {
  .hr-bar {
    height: 2vh;
    margin-top: -10%;
  }

  .grid-container {
    display: grid;
    grid-template-columns: auto auto;
    padding: 10px;
  }

  .info-card h3 {
    font-size: 1.8vw;
  }

  .info-card p {
    font-size: 1.4vw;
  }
}
